.content {
  padding-left: 10%;
  width: 60%;
}

.image {
  height: 20em;
  width: 100%;
  object-fit: cover;
  box-shadow: 10px 10px var(--green);
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1;
}

.item {
  width: 90%;
  padding: 0px 20px;
}

.itemImg {
  width: 85%;
  padding: 10px 20px;
}

.aboutText {
  text-decoration: none;
  color: var(--green);
  font-weight: 700;
  transition: 0.2s color;
}

@media only screen and (max-width: 700px) {
    .image {
      height: 15em;
    }
    .row {
      display: block;
    }
}
