:root {
  --white: rgba(255,255,255,255);
  --gray: rgba(158, 158, 158, 255);
  --blue: rgba(175, 149, 237, 255);
  --green: #CCFF90;
  --orange: rgba(252, 164, 180, 255);
  --light-blue: #80DEEA;
  --sidebar: rgb(50, 55, 56);
  --pastel-red: rgb(230,105,82);
  --text-color: rgb(208,208,208);
  --light-red: rgb(230,151,136);
}

body {
  color: var(--text-color);
  background-color: rgb(24,26,27);
}

.colorbar {
  z-index: -1;
  height: 100%;
  position: fixed;
  border-left: 20px solid var(--sidebar);
}

div.navbar-main {
  padding: 3% 10% 0% 4%;
}

.navbar-main {
  display: flex;
  width: 6%;
  flex-direction: column;
}

.navitem {
  opacity: 0.7;
  font-weight: 400;
}

.inline {
  display: inline-flex;
}

.active-navitem, .active-navitem:hover {
  opacity: 1;
  font-weight: 700;
}

.flex-container {
  margin-top: -6%;
  display: flex;
  justify-content: center;
}

.wrap-content {
  flex-wrap: wrap;
}

.fadediv {
  animation: fadein ease 0.5s;
}

@keyframes fadein {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

.underline {
  color: var(--white);
  text-decoration: none;
  text-align: left;
}

.imageWrapper {
  width: 100%;
  position: relative;
  background-color: var(--gray);
}

.image {
  width: 100%;
  position: absolute;
}

/*COLOR UNDERLINE*/
.underline-blue {
  background:
     linear-gradient(
       to bottom, var(--pastel-red) 0%,
       var(--pastel-red) 100%
     );
   background-position: 0 100%;
   background-repeat: repeat-x;
   background-size: 4px 4px;
   transition: background-size .4s;
}

.underline-purple {
  background:
     linear-gradient(
       to bottom, var(--green) 0%,
       var(--green) 100%
     );
   background-position: 0 100%;
   background-repeat: repeat-x;
   background-size: 4px 4px;
   transition: background-size .4s;
}

.underline-orange {
  background:
     linear-gradient(
       to bottom, var(--blue) 0%,
       var(--blue) 100%
     );
   background-position: 0 100%;
   background-repeat: repeat-x;
   background-size: 4px 4px;
   transition: background-size .4s;
}

.underline-yellow {
  background:
     linear-gradient(
       to bottom, var(--light-blue) 0%,
       var(--light-blue) 100%
     );
   background-position: 0 100%;
   background-repeat: repeat-x;
   background-size: 4px 4px;
   transition: background-size .4s;
}

.underline-pastelpink {
  background:
     linear-gradient(
       to bottom, var(--blue) 0%,
       var(--blue) 100%
     );
   background-position: 0 100%;
   background-repeat: repeat-x;
   background-size: 4px 4px;
   transition: background-size .4s;
   margin-bottom: -5px;
}

.underline:hover {
  background-size: 10px 10px;
}

/*TEXT*/
h1 {
  font-size: 28px;
}

h2 {
  font-size: 20px;
}

p, li {
  font-size: 13px;
}

ul {
  padding-left: 10px;
}

a {
  text-decoration: none;
  color: var(--light-red);
  font-weight: 700;
  transition: 0.2s color;
}

a:hover {
  color: var(--white);
}

/*ANIMATIONS*/
.hvr-grow {
    transform: translateZ(0);
    transition: transform 0.3s;
    cursor: pointer;
}

.hvr-grow:hover,
.hvr-grow:focus,
.hvr-grow:active {
    transform: scale(1.1);
}

.hvr-backward {
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  transition-property: transform;
}
.hvr-backward:hover, .hvr-backward:focus, .hvr-backward:active {
  -webkit-transform: translateX(-8px);
  transform: translateX(-8px);
}
