.content {
  width: 40%;
}

.socialItem {
  font-size: 1.5em;
  margin-right: 12px;
}

.subtitle {
  margin-bottom: -15px;
}

.text {
  width: 75%;
}

.titleListContainer {
  overflow-y: hidden;
  width: 200%;
  height: 38px;
  margin-top: -58px;
  margin-left: 5px;
}

.titleListContainer::-webkit-scrollbar {
  display: none;
}

.titleListContainer {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.titleList {
  text-align: left;
  margin-top: 0;
  list-style: none;
  padding-left: 195px;
  -webkit-animation: change 8s infinite;
  animation: change 8s infinite;
  transition-timing-function: ease-in;
}

@media only screen and (max-width: 700px) {
    .titleList { padding-left: 0px; }
    .titleListContainer { margin-top: -15px; }
}

.title li {
  white-space: nowrap;
  line-height: 40px;
  font-size: 1em;
  color: var(--light-red);
}

.title li:nth-child(2n) {
  color: var(--green);
}

.title li:nth-child(3n) {
  color: var(--light-blue);
}

.title li:nth-child(4n) {
  color: var(--blue);
}

@-webkit-keyframes opacity {
  0%, 100% { opacity: 0; }
  50% { opacity: 1; }
}

@-webkit-keyframes change {
  0%, 20%, 100% {
    -webkit-transform: translate3d(0, 0%, 0);
        transform: translate3d(0, 0%, 0);
  }
  25%, 45% {
    -webkit-transform: translate3d(0, -25%, 0);
        transform: translate3d(0, -25%, 0);
  }
  50%, 70% {
    -webkit-transform: translate3d(0, -50%, 0);
        transform: translate3d(0, -50%, 0);
  }
  75%, 90% {
    -webkit-transform: translate3d(0, -75%, 0);
        transform: translate3d(0, -75%, 0);
  }
}

@keyframes opacity {
  0%, 100% { opacity: 0; }
  50% { opacity: 1; }
}

@keyframes change {
  0%, 20%, 100% {
    -webkit-transform: translate3d(0, 0%, 0);
        transform: translate3d(0, 0%, 0);
  }
  25%, 45% {
    -webkit-transform: translate3d(0, -25%, 0);
        transform: translate3d(0, -25%, 0);
  }
  50%, 70% {
    -webkit-transform: translate3d(0, -50%, 0);
        transform: translate3d(0, -50%, 0);
  }
  75%, 90% {
    -webkit-transform: translate3d(0, -75%, 0);
        transform: translate3d(0, -75%, 0);
  }
}
