.content {
  width: 50%;
}

.grid {
  display: grid;
  grid-template-columns: 200px 200px 200px;
  grid-template-rows: 285px 285px 285px;
  grid-gap: 4px;
}

@media only screen and (max-width: 650px) {
    .grid {
      grid-template-columns: 200px 200px;
      grid-template-rows: 285px 285px 285px 285px;
    }
}
