.content {
  width: 40%;
}

.grid {
  display: grid;
  grid-template-columns: 160px 160px 160px;
  grid-template-rows: 160px 160px;
  grid-gap: 14px;
}

@media only screen and (max-width: 700px) {
    .grid {
      grid-template-columns: 160px 160px;
      grid-template-rows: 160px 160px 160px;
    }
}

/*CARD*/

.cardImg {
  width: 50%;
}

.cardContent {
  width: 40%;
}

.backBtn {
  color: var(--brown);
  font-size: 25px;
}

.link a {
  margin-right: 5px;
}
